.chronometer {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.display {
  font-size: 4rem;
  text-align: center;
  color: #333;
  padding: 5rem 0;

  @media (min-width: 768px) {
    font-size: 5rem;
  }
}

.lab-list {
  flex-grow: 1;
  overflow: scroll;
  max-height: calc(100vh - (80px + 236px + 90px));

  @media (min-width: 768px) {
    max-height: calc(100vh - (80px + 255px + 90px));
  }
}
